export class PortfolioItem {

  public image: string;
  public link: string;
  public title: string;
  public note: string;

  public constructor(image: string, link: string, title: string, note = '"') {
    this.image = image;
    this.link = link;
    this.title = title;
    this.note = note;
  }

}



