import { Component } from '@angular/core';
import {ImageLink} from "./models/image_link";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';


  footer_links: ImageLink[] = [
    new ImageLink('linkedin.png', 'http://www.linkedin.com/in/albertrosa', 'My Linked In', 'L', 'linked'),
    new ImageLink('twitter.png', 'http://www.twitter.com/albertrosa', 'twitter', 'T', 'twitter'),
    new ImageLink('facebook.png', 'http://www.facebook.com/albertrosa', 'facebook', 'F', 'facebook')
  ];

}



