export class ImageLink {

  image: string;
  link : string;
  title: string;
  text: string;
  cssClass: string;

  public constructor (image: string, link: string, title: string, text="", cssClass = "") {
    this.image = image;
    this.link = link;
    this.title = title;
    this.text = text;
    this.cssClass = cssClass;
  }

}



